import styled from '@emotion/styled';

export const Form = styled.form`
  position: absolute;  
  bottom: 50%;  
  right: 0;
  width: 100%;
`;

export const Btn = styled.div`
  position: absolute;
  margin: auto;
  margin-right: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 50px;
  background-color: ${p => p.theme.color.bgBtnSearch};
  border-radius: 50%;
  transition: all 1s;
  z-index: 4;

  &:hover {
    cursor: pointer;
  }

  &::before {
    content: "";
    position: absolute;
    margin: auto;
    top: 18px;
    right: 0;
    bottom: 0;
    left: 18px;
    width: 10px;
    height: 3px;
    background-color: ${p => p.theme.color.textPrimaryLight};
    transform: rotate(45deg);
    transition: all 0.5s;
  }

  &::after {
    content: "";
    position: absolute;
    margin: auto;
    top: -5px;
    right: 0;
    bottom: 0;
    left: -5px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 3px solid ${p => p.theme.color.textPrimaryLight};
    transition: all 0.5s;
  }
`;

export const Input = styled.input`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 40px;
  height: 40px;
  margin: auto;
  margin-right: 5px;
  padding: 0 50px 0 10px;
  outline: none;
  border: none;
  border-radius: 10px;
  font-size: ${p => p.theme.fs.s};
  font-weight: ${p => p.theme.fw.bold};
  color: ${p => p.theme.color.textSecondary};
  background-color: ${p => p.theme.color.bgInputSearch};
  box-shadow: 0 0 25px 0 crimson,
              0 20px 25px 0 rgba(0, 0, 0, 0.2);
  opacity: 0;
  z-index: 5;
  transition: all 1s;

  &:hover {
    cursor: pointer;
  }

  &:focus {
    width: calc(100% - 5px);
    opacity: 1;
    cursor: text;
  }

  &:focus ~ div {
    background-color: ${p => p.theme.color.bgBtnClose};
    z-index: 6;

    &::before {
      top: 0;
      left: 0;
      width: 25px;
    }
    &::after {
      top: 0;
      left: 0;
      width: 25px;
      height: 3px;
      border: none;
      background-color: ${p => p.theme.color.textPrimaryLight};
      border-radius: 0%;
      transform: rotate(-45deg);
    }
  }

  &::placeholder {
    color: ${p => p.theme.color.textSecondary};
    font-size: ${p => p.theme.fs.s};
    font-weight: ${p => p.theme.fw.bold};
   
  }
`;