const makeFirstLetterUppercase = (string: string): string => {
    return string.charAt(0).toUpperCase() + string.slice(1, string.length);
};

const addPositiveSign = (number: number): string => {
  if (number <= 0) return number.toString();
  return "+" + number.toString();
};

const getPressureDescription = (value: number): string => {
  if (Math.round(value * 0.75006375541921) < 760) return " ";
  if (Math.round(value * 0.75006375541921) > 760) return " ";
  return "  "
};

export const getHumidityDescription = (level: number): string => {
  if (level < 30) return "  ";
  if (level >= 30 && level <= 60) return "";
  return " ";
};

export const getVisibilityDescription = (value: number): string => {
  if (value <= 50) return '';
  if (value > 50 && value <= 500) return 'C ';
  if (value > 500 && value <= 2000) return ' ';
  if (value > 2000 && value <= 9000) return 'B ';
  return '  ';
};

export const getWindDescription = (deg: number, gust: number): string => {
  let direction: string = "";
  let gusts: string = "-";
  
  if (gust) {
    gusts = `${Math.round(gust)}/`;
  }

  if (deg > 15 && deg <= 75) direction = "-";
  if (deg > 76 && deg <= 105) direction = "";
  if (deg > 105 && deg <= 165) direction = "-";
  if (deg > 166 && deg <= 195) direction = "";
  if (deg > 195 && deg <= 255) direction = "-";
  if (deg > 255 && deg <= 285) direction = "";
  if (deg > 285 && deg <= 345) direction = "-";
  return `${direction},  ${gusts}`;
};

export const getUviDescription = (uvi: number) => {
  let risk = ``;
  if (uvi >= 0 && uvi < 3) risk = '';
  if (uvi >= 3 && uvi < 6) risk = '';
  if (uvi >= 6 && uvi < 8) risk = '';
  if (uvi >= 8 && uvi < 11) risk = ' ';
  return `${risk} `;
};

export const getCloudsDescription = (clouds: number) => {
  if (clouds <= 10) return " ";
  if (clouds > 10 && clouds <= 30) return "";
  if (clouds > 30 && clouds <= 70) return " ";
  return "";
};

export const helpers = {
  makeFirstLetterUppercase,
  addPositiveSign,
  getPressureDescription,
  getHumidityDescription,
  getVisibilityDescription,
  getWindDescription,
  getUviDescription,
  getCloudsDescription
};

export default helpers;